
import React from 'react';
import ProjectNav from './ProjectNav';
import SyntaxHighligher from 'react-syntax-highlighter'
import { nightOwl } from 'react-syntax-highlighter/dist/cjs/styles/hljs';

function LocalStorage() {

    const lsJs = `
    // default homepage is business, so only redirect if personal value set
var bookmark = localStorage.getItem('FNBDixonHomepage');
var mark = window.location.pathname;

if (bookmark === "personal" && mark === "/") {
    window.location.href = "/personal";
}
// local storage cookie for homepages
var homepageBtn = "<div class='homepageBtn'><i class='fa fa-home'></i> Make This My Banking Homepage</div>";

// put the buttons on initial load if value not set already
if (bookmark == "personal" || bookmark == "business") {
    // cookie already set, no buttons
} else {
    if ($f('#homePage').hasClass("personal")) {
        $f('#header .primaryNav > li.personal').before(homepageBtn);
    } else {
        $f('#header .primaryNav > li.business').before(homepageBtn);
    }
}

// if no one clicks them after X seconds, fade them out
setTimeout(function() {
    $f('.homepageBtn').fadeOut(1500);
}, 5000);

// set localStorage value on click then fade out buttons
$f('#header').delegate('.homepageBtn', 'click', function() {
    localStorage.removeItem('FNBDixonHomepage');

    if ($f('#homePage.personal').length) {
        localStorage.setItem('FNBDixonHomepage','personal');
    } else {
        localStorage.setItem('FNBDixonHomepage','business');
    }

    $f(this).fadeOut(1500);
});

$f('.primaryNav > li.business > a').on('click', function() {
    if (bookmark == 'personal') {
        // need to unset the cookie or you can never see the biz section
        localStorage.removeItem('FNBDixonHomepage');
    }
})`;

    return (
        <div className="project-detail">
            <ProjectNav />
            <section className="project-header">
                <h2>FNB Dixon</h2>
                <p className="project-summary">
                This site design included two homepages, one for personal users and one targeted for business users. I used the browser's localStorage to allow the user to choose their default homepage.                    <br/>
                 <br/><br/>   <img src="images/dixon.png" alt="FNB homepage" />
                    <a className="site-link" href="https://www.thatsmybank.com" target="_blank" rel="noreferrer">Visit Site <i className="fas fa-external-link-alt"></i></a>
                </p>
            </section>
            <section className="project-code">
                <h4>Javascript</h4>
               <SyntaxHighligher language="js" style={nightOwl} wrapLongLines="true">
                {lsJs}
               </SyntaxHighligher>
            </section>
        </div>
    )
}

export default LocalStorage;