
import React from 'react';
import ProjectNav from './ProjectNav';
import SyntaxHighligher from 'react-syntax-highlighter'
import { nightOwl } from 'react-syntax-highlighter/dist/cjs/styles/hljs';

function FirstNational() {

    const FnbJs = `
    var el = $f('.slideContent');
el.on('load', hintBrowser);
el.on('animationEnd', removeHint);

function hintBrowser() { this.style.willChange = 'transform'; }

function removeHint() { this.style.willChange = 'auto'; }

var animation_elements = $f('.fp-image, .fp-image-large');
var the_window = $f(window);

function check_if_in_view() {
    var window_height = the_window.height();
    var window_top_position = the_window.scrollTop();
    var window_bottom_position = (window_top_position + window_height);
    
    $f.each(animation_elements, function() {
        var element = $f(this);
        var element_height = element.outerHeight();
        var element_top_position = element.offset().top;
        var element_bottom_position = (element_top_position + element_height);
        
        //check to see if this current container is within viewport
        if ((element_bottom_position >= window_top_position) &&
            (element_top_position <= window_bottom_position)) {
            element.addClass('in-view');
        } else {
            element.addClass('out-view');
        }
    });
}

the_window.on('scroll resize', check_if_in_view);
the_window.trigger('scroll');`;

    return (
        <div className="project-detail">
            <ProjectNav />
            <section className="project-header">
                <h2>First National Bank</h2>
                <p className="project-summary">
                I created a custom CSS-based animation for the homepage banner and a jQuery/CSS solution for features that fade in as the user scrolls.<br/>
                    <br/>
                    <img src="images/henning.jpg" alt="FNB homepage" />
                    <a className="site-link" href="https://www.fnbhenning.com" target="_blank" rel="noreferrer">Visit Site <i className="fas fa-external-link-alt"></i></a>
                </p>
            </section>
            <section className="project-code">
                <h4>Javascript</h4>
               <SyntaxHighligher language="js" style={nightOwl} wrapLongLines="true">
                {FnbJs}
               </SyntaxHighligher>
            </section>
        </div>
    )
}

export default FirstNational;