
import React from 'react';
import ProjectNav from './ProjectNav';
import SyntaxHighligher from 'react-syntax-highlighter'
import { nightOwl } from 'react-syntax-highlighter/dist/cjs/styles/hljs';

function ExchangeBank() {

    const ExchangeScss = `
    .bg {
        width: 50vw;
        background-size: cover;
        background-position: center top;
        transition: opacity .25s;
        &.fp-1 { z-index: 4; opacity: 1; }
        &.fp-2 { z-index: 3; opacity: 0; }
        &.fp-3 { z-index: 2; opacity: 0; }
        &.fp-4 { z-index: 1; opacity: 0; }
    }
    
    .fp {
        z-index: 5;
        .icon {
            background: $darkBlue;
            transition: background-color .25s;
        }
    
        &:hover {
            h3 a { color: $tan; }
            .icon { background-color: $tan; }
            + .bg { z-index: 4; opacity: 1; }
        }
    }`;

    return (
        <div className="project-detail">
            <ProjectNav />
            <section className="project-header">
                <h2>Exchange Bank</h2>
                <p className="project-summary">
                This custom promotional area displays a different image when each feature is interacted with, 
                I created a CSS-only solution for the transitions while keeping the area client-editable in the CMS.<br/>
                    <br/>
                    <img src="images/exchange.jpg" alt="Exchange bank homepage" />
                    <a className="site-link" href="https://www.myexchangebank.com" target="_blank" rel="noreferrer">Visit Site <i className="fas fa-external-link-alt"></i></a>
                </p>
            </section>
            <section className="project-code">
                <h4>Sass</h4>
               <SyntaxHighligher language="scss" style={nightOwl} wrapLongLines="true">
                {ExchangeScss}
               </SyntaxHighligher>
            </section>
        </div>
    )
}

export default ExchangeBank;