import React, { useState } from 'react'
import './App.scss';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Header, Tifb, LocalChart, ExchangeBank, JQueryES6, FirstNational, LocalStorage } from "./components";
import Portfolio from './components/Portfolio';

function App() {
  
  const [darkTheme, setDarkTheme] = useState(false);
  const toggleTheme = () => setDarkTheme(!darkTheme);

  return (
    <div className="App" data-theme={darkTheme ? 'dark' : 'light'}>
      <Router>
        <Header />
         <div className="container">
         <button onClick={toggleTheme} className="btn-switch">{darkTheme ? 'Dark' : 'Light'}</button>
          <Switch>
            <Route path="/" exact component={() => <Portfolio />} />
            <Route path="/tifb" exact component={() => <Tifb />} />
            <Route path="/thelocal" exact component={() => <LocalChart />} />
            <Route path="/exchange" exact component={() => <ExchangeBank />} />
            <Route path="/jqueryes6" exact component={() => <JQueryES6 />} />
            <Route path="/firstnational" exact component={() => <FirstNational />} />
            <Route path="/localstorage" exact component={() => <LocalStorage />} />
          </Switch>
          </div>
      </Router>
      
    </div>
  );
}

export default App;
